@use "base/variables" as v;
@use "base/mixins" as m;

.table {
  width: 100%;
  border-collapse: collapse;

  &__thead {
    background-color: v.$gris-oscuro;
    color: v.$blanco;
  }

  &__th {
    padding: 1rem;
    text-align: left;
  }

  &__td {
    padding: 1.5rem 1rem;

    &--acciones {
        @extend .table__td;
        display: flex;
        justify-content: flex-end;
        gap: 2rem;
        align-items: center;
    }
  }

  &__tr {
    &:nth-child(even) {
        background-color: v.$gris-claro;
    }
  }

  &__accion {
    all: unset;
    text-transform: uppercase;
    font-size: 1.4rem;
    font-weight: v.$bold;
    cursor: pointer;
    &--editar {
      color: v.$azul;
    }

    &--eliminar {
      color: v.$rojo;
    }
  }
}
